.nav-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 66px;
  background-color: #f5f5f5;
}

.nav-tab__links {
  display: flex;
  flex-direction: row;
  gap: 40px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav-tab__link {
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.23;
  text-decoration-line: underline;
  color: #000;
}

@media screen and (max-width: 500px) {
  .nav-tab__links {
    gap: 24px;
  }
  .nav-tab__link {
    font-size: 11px;
    line-height: 16px;
  }
}
