.portfolio {
  margin: 0;
  padding: 100px 0 0;
}

.portfolio__title {
  margin: 0;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.11;
  color: #a0a0a0;
}

.portfolio__links {
  margin: 50px 0 0;
  list-style-type: none;
  padding-inline-start: 0;
}

.portfolio__list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
  padding: 20px 0 19px;
}

.portfolio__list-item:first-of-type {
  padding-top: 0;
}

.portfolio__list-item:last-of-type {
  padding-bottom: 0;
  border-bottom: none;
}

.portfolio__link {
  width: 100%;
  font-weight: normal;
  font-size: 30px;
  line-height: 1.67;
  letter-spacing: -0.04em;
  color: #000;
  text-decoration: none;
}

.portfolio__icon-link {
  width: 30px;
  height: 30px;
  background: url("../../images/icon-link-portfolio.svg") no-repeat center 60%;
}

@media screen and (max-width: 1000px) {
  .portfolio {
    padding-top: 90px;
  }
  .portfolio__link {
    font-size: 28px;
    line-height: 50px;
  }
}

@media screen and (max-width: 500px) {
  .portfolio {
    padding-top: 70px;
  }
  .portfolio__title {
    font-size: 14px;
    line-height: 20px;
  }
  .portfolio__links {
    margin-top: 40px;
  }
  .portfolio__link {
    font-size: 18px;
    line-height: 28px;
  }
  .portfolio__icon-link {
    width: 20px;
    height: 20px;
    background-position: top right;
    background-size: 65%;
  }
}
